// app/portal/cases/[id]/page.tsx
import { prisma } from "@/lib/prisma";
import { getSession } from "@/lib/auth";
import { notFound } from "next/navigation";
import Link from "next/link";
import nextDynamic from "next/dynamic"; // ⬅️ alias to avoid conflict with export const dynamic
import UploadButtons from "@/components/UploadButtons";
import ProgressTracker from "@/components/ProgressTracker";
import ActionsBar from "@/components/ActionsBar";
import CommentsPanel from "@/components/CommentsPanel";

export const dynamic = "force-dynamic"; // ⬅️ keep this exact name for Next.js route config

function fmtDate(d?: Date | null) {
  if (!d) return "—";
  return new Date(d).toLocaleString();
}

/** Picks the correct model getter no matter how Prisma named it */
function getCaseModel(): any {
  const client: any = prisma as any;
  return client.dentalCase ?? client.case ?? client.case_;
}

type Params = Promise<{ id: string }>;

// Use the aliased import here
const STLViewer = nextDynamic(() => import("@/components/STLViewer"), { ssr: false });

export default async function CaseDetailPage({ params }: { params: Params }) {
  const session = await getSession();
  if (!session) return notFound();

  // Next 15: params is async
  const { id } = await params;
  if (!id) return notFound();

  const model = getCaseModel();

  const item = await model.findUnique({
    where: { id },
    include: {
      clinic: true,
      files: { orderBy: { createdAt: "asc" } },
      events: { orderBy: { at: "asc" } },
      comments: {
        orderBy: { createdAt: "asc" },
        select: {
          id: true,
          body: true,
          createdAt: true,
          author: { select: { id: true, email: true, role: true } },
        },
      },
    },
  });

  if (!item) return notFound();

  // Doctors may view ONLY their own cases (strict)
  if (session.role === "customer") {
    if (!item.doctorUserId || item.doctorUserId !== session.userId) {
      return notFound();
    }
  }

  const isLabOrAdmin = session.role === "lab" || session.role === "admin";
  const scanLocked = item.stage !== "DESIGN";

  const byLabel = (label: string) => item.files.filter((f: any) => f.label === label);
  const scanFiles = byLabel("SCAN");
  const modelPlusDesignFiles = byLabel("MODEL_PLUS_DESIGN");
  const designOnlyFiles = byLabel("DESIGN_ONLY");

  // Choose a file to preview in 3D
  const previewUrl =
    designOnlyFiles[0]?.url ||
    modelPlusDesignFiles[0]?.url ||
    scanFiles[0]?.url ||
    null;

  return (
    <section className="space-y-6">
      <header className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-semibold">{item.patientAlias}</h1>
          <p className="text-white/70">
            Clinic: {item.clinic.name} • Teeth: {item.toothCodes}
          </p>
          <p className="text-white/50">Status: {item.status} • Due: {fmtDate(item.dueDate)}</p>
        </div>
        <Link href="/portal/cases" className="text-white/80 underline">
          ← Back to Cases
        </Link>
      </header>

      {/* Progress tracker */}
      <ProgressTracker
        caseId={item.id}
        stage={item.stage as any}
        role={session.role as any}
      />

      {/* Main content: 3D preview + uploads */}
      <div className="grid md:grid-cols-3 gap-6">
        <div className="md:col-span-2 rounded-xl border border-white/10 p-4">
          <h2 className="font-medium mb-3">Preview</h2>
          {previewUrl ? (
            <div className="h-[420px] rounded-lg overflow-hidden bg-black/30">
              {previewUrl.toLowerCase().endsWith(".stl") ? (
                <STLViewer url={previewUrl} />
              ) : (
                <div className="p-4">
                  <p className="text-white/70 mb-2">
                    Preview is only for STL right now. Download to view locally:
                  </p>
                  <a className="underline" href={previewUrl} target="_blank">
                    {previewUrl.split("/").pop()}
                  </a>
                </div>
              )}
            </div>
          ) : (
            <p className="text-white/60">No file to preview yet.</p>
          )}
        </div>

        {/* Upload slots (lab/admin only) */}
        <div className="space-y-4">
          {isLabOrAdmin && (
            <>
              <UploadButtons
                caseId={item.id}
                label="SCAN"
                title="Upload / Replace Scan"
                accept=".stl,.ply,.obj"
                disabled={scanLocked}
              />
              <UploadButtons
                caseId={item.id}
                label="MODEL_PLUS_DESIGN"
                title="Upload Model + Design (STL)"
                accept=".stl"
              />
              <UploadButtons
                caseId={item.id}
                label="DESIGN_ONLY"
                title="Upload Design Only (STL)"
                accept=".stl"
              />
            </>
          )}

          {/* Always show what's attached */}
          <div className="rounded-xl border border-white/10 p-4">
            <h2 className="font-medium mb-2">Files</h2>
            <div className="space-y-3 text-white/80">
              <div>
                <div className="text-white/60 text-sm">Scan</div>
                {scanFiles.length === 0 ? (
                  <div className="text-white/60">—</div>
                ) : (
                  <ul className="list-disc list-inside">
                    {scanFiles.map((f: any) => (
                      <li key={f.id}>
                        <a className="underline" href={f.url} target="_blank">
                          {f.url.split("/").pop()}
                        </a>
                      </li>
                    ))}
                  </ul>
                )}
              </div>

              <div>
                <div className="text-white/60 text-sm">Model + Design</div>
                {modelPlusDesignFiles.length === 0 ? (
                  <div className="text-white/60">—</div>
                ) : (
                  <ul className="list-disc list-inside">
                    {modelPlusDesignFiles.map((f: any) => (
                      <li key={f.id}>
                        <a className="underline" href={f.url} target="_blank">
                          {f.url.split("/").pop()}
                        </a>
                      </li>
                    ))}
                  </ul>
                )}
              </div>

              <div>
                <div className="text-white/60 text-sm">Design Only</div>
                {designOnlyFiles.length === 0 ? (
                  <div className="text-white/60">—</div>
                ) : (
                  <ul className="list-disc list-inside">
                    {designOnlyFiles.map((f: any) => (
                      <li key={f.id}>
                        <a className="underline" href={f.url} target="_blank">
                          {f.url.split("/").pop()}
                        </a>
                      </li>
                    ))}
                  </ul>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Actions (Approve / Request Changes) */}
      <ActionsBar
        caseId={item.id}
        role={session.role as any}
        status={item.status as any}
      />

      {/* Discussion/comments */}
      <CommentsPanel
        caseId={item.id}
        canPost={true}
        comments={(item.comments || []).map((c: any) => ({
          id: c.id,
          body: c.body,
          at: c.createdAt,
          author: c.author?.email || "unknown",
          role: c.author?.role || "customer",
        }))}
      />

      {/* Status timeline */}
      <div className="rounded-xl border border-white/10 p-4">
        <h2 className="font-medium mb-2">Status Timeline</h2>
        {item.events.length === 0 ? (
          <p className="text-white/60">No events yet.</p>
        ) : (
          <ol className="text-white/80 space-y-1">
            {item.events.map((ev: any) => (
              <li key={ev.id}>
                {ev.from ? `${ev.from} → ` : ""}{ev.to} • {fmtDate(ev.at)} {ev.note ? `• ${ev.note}` : ""}
              </li>
            ))}
          </ol>
        )}
      </div>
    </section>
  );
}
